.colorspace-labels {
  -ms-flex-order: 1;
      order: 1;
  display: -ms-flexbox;
  display: flex;
  margin: 5ex auto;
}

.colorspace-labels > div > input[type=radio] {
  display: none;
  margin: 0;
}

.colorspace-labels > div > label {
  padding: 1ex 20px;
  margin: 0 1px 0 0;
  background: #d7dcdf;
  -webkit-transition: all .25s ease;
  -o-transition: all .25s ease;
  transition: all .25s ease;
}

.colorspace-labels > div > label:hover {
  background: #a5acb1;
}

.colorspace-labels > div > input[type=radio]:checked + label {
  background: #02b3e4;
}

.colorspace-labels > div > label:hover {
  background: #a5acb1;
}

.colorspace-labels > div > input[type=radio]:checked + label{
  background: #02b3e4;
}

.colorspace-labels > div:first-child > label {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}

.colorspace-labels > div:last-child > label {
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}



